/**
 * @file 将SortableJs转化为React组件
 * @author cherry.zj
 */

import * as React from 'react';
import SortableJS from 'sortablejs';

const store = {
  nextSibling: null,
  activeComponent: null,
};

class SortableProps {
  /** 拖拽的设置，可参考 https://github.com/SortableJS/Sortable */
  options?: object;
  /** 容器的类型，默认是div */
  tagName?: string;
  /** 容器的class */
  className?: string;
  /** 拖拽排序时变化回调 */
  onChange?: (items: any[], sortable, evt) => void;
}

export default class SortableList extends React.Component<SortableProps, any> {
  static defaultProps = {
    options: {},
    tagName: 'div',
  };

  sortable = null;
  node;

  componentDidMount() {
    const options = { ...this.props.options };
    [
      'onChoose',
      'onStart',
      'onEnd',
      'onAdd',
      'onUpdate',
      'onSort',
      'onRemove',
      'onFilter',
      'onMove',
      'onClone',
    ].forEach(name => {
      const eventHandler = options[name];
      options[name] = (...params) => {
        const [evt] = params;
        if (name === 'onChoose') {
          store.nextSibling = evt.item.nextElementSibling;
          store.activeComponent = this;
        } else if ((name === 'onAdd' || name === 'onUpdate') && this.props.onChange) {
          const items = this.sortable.toArray();
          const remote = store.activeComponent;
          const remoteItems = remote.sortable.toArray();
          const referenceNode = store.nextSibling && store.nextSibling.parentNode !== null ? store.nextSibling : null;
          evt.from.insertBefore(evt.item, referenceNode);
          if (remote !== this) {
            const remoteOptions = remote.props.options || {};
            if (typeof remoteOptions.group === 'object' && remoteOptions.group.pull === 'clone') {
              // Remove the node with the same data-reactid
              evt.item.parentNode.removeChild(evt.item);
            }
            remote.props.onChange && remote.props.onChange(remoteItems, remote.sortable, evt);
          }
          this.props.onChange && this.props.onChange(items, this.sortable, evt);
        }
        if (evt.type === 'move') {
          const [originalEvent] = params;
          const canMove = eventHandler ? eventHandler(evt, originalEvent) : true;
          return canMove;
        }
        setTimeout(() => {
          eventHandler && eventHandler(evt);
        }, 0);
      };
    });
    this.sortable = SortableJS.create(this.node, options);
  }

  shouldComponentUpdate(nextProps) {
    if (!nextProps.onChange) {
      return false;
    }
    return true;
  }

  componentWillUnmount() {
    if (this.sortable) {
      this.sortable.destroy();
      this.sortable = null;
    }
  }

  render() {
    const { tagName: Component, options, onChange, ...props } = this.props;

    return (
      <Component
        {...props}
        ref={node => {
          this.node = node;
        }}
      />
    );
  }
}

