import * as React from 'react';
import { Tabs } from 'antd';
import { Icon } from '../../Icon';
import { NotificationType } from './definitions';
import { getNotificationTypeNameWithCount, getEmptyText, normalizeNotification, getLink } from './utils';
import { NotificationItem } from './NotificationItem';
import Empty from '../Empty';
import * as classNames from 'classnames';
import I18N from '../../utils/I18N';

const TabPane = Tabs.TabPane;

interface CountsBySources {
  [key: string]: number;
}

export interface NotificationInfo {
  messageCounts: CountsBySources;
  taskCounts: CountsBySources;
  messages: Notification[];
  tasks: Notification[];
  messagesLoading: boolean;
  tasksLoading: boolean;
}

export default class NotificationBoxView extends React.Component<NotificationInfo> {
  state = {
    visible: false,
    activeNotificationType: NotificationType.Message,
  };

  container;

  componentDidMount() {
    document.addEventListener('click', this.hideOnOutsideClick);
  }

  componentWillUnmount() {
    document.removeEventListener('click', this.hideOnOutsideClick);
  }

  hideOnOutsideClick = (e: MouseEvent) => {
    const { visible } = this.state;
    if (visible && !this.container.contains(e.target as any)) {
      this.setState({
        visible: false,
      });
    }
  };

  handleNotificationTypeChange = (notificationType: NotificationType) => {
    this.setState({
      activeNotificationType: notificationType,
    });
  };

  renderNotificationList(notificationType: NotificationType) {
    const { messages, tasks, messagesLoading, tasksLoading } = this.props;
    const data = notificationType === NotificationType.Message ? messages : tasks;
    const loading = notificationType === NotificationType.Message ? messagesLoading : tasksLoading;
    const notifications = data || [];
    const shouldRenderEmpty = !loading && notifications.length === 0;
    if (shouldRenderEmpty) {
      return <Empty text={getEmptyText(notificationType)} />;
    }
    return (
      <div className="notification-list">
        {notifications.map(notification => {
          const normalizedNotification = normalizeNotification(notification as any, notificationType);
          return (
            normalizedNotification && (
              <NotificationItem
                key={normalizedNotification.id}
                icon={normalizedNotification.icon}
                title={normalizedNotification.sourceName}
                time={normalizedNotification.time}
                detail={normalizedNotification.title}
                url={normalizedNotification.url}
              />
            )
          );
        })}
      </div>
    );
  }

  renderContent() {
    const { messageCounts, taskCounts } = this.props;
    return (
      <div
        className={classNames({
          'notification-box-content': true,
          'notification-box-content-hidden': !this.state.visible,
        })}>
        <div className="notification-box-header">{I18N.NotificationBox.title}</div>
        <div className="notification-box-body">
          <Tabs activeKey={this.state.activeNotificationType} onChange={this.handleNotificationTypeChange}>
            {Object.values(NotificationType).map(notificationType => {
              const counts = notificationType === NotificationType.Message ? messageCounts : taskCounts;
              const totalCount = Object.values(counts).reduce((prev, curr) => prev + curr, 0);
              return (
                <TabPane key={notificationType} tab={getNotificationTypeNameWithCount(notificationType, totalCount)}>
                  {this.renderNotificationList(notificationType)}
                </TabPane>
              );
            })}
          </Tabs>
        </div>
        <div className="notification-box-footer">
          <a href={getLink(this.state.activeNotificationType)} target="_blank" className="notification-box-view-all">
            {I18N.NotificationBox.viewAll}
          </a>
        </div>
      </div>
    );
  }

  toggleNotificationBox = () => {
    this.setState({
      visible: !this.state.visible,
    });
  };

  renderNotificationsCount() {
    const { messageCounts, taskCounts } = this.props;
    let totalCount = 0;
    [messageCounts, taskCounts].forEach(counts => {
      totalCount += Object.values(counts).reduce((prev, curr) => prev + curr, 0);
    });
    if (totalCount === 0 || isNaN(totalCount)) {
      return null;
    }
    const countText = totalCount < 100 ? totalCount : '...';
    return <span className="notification-box-count">{countText}</span>;
  }

  render() {
    return (
      <div className="notification-box" ref={ref => (this.container = ref)}>
        <div className="notification-box-trigger" onClick={this.toggleNotificationBox}>
          <Icon type="oned-icon icon-news-specia" />
          {this.renderNotificationsCount()}
        </div>
        {this.renderContent()}
      </div>
    );
  }
}

