export function getShortName(text: string, length = 14) {
  if (!text || typeof text !== 'string') {
    return '';
  }
  const result = [];
  let currLength = 0;

  let i = 0;
  while (currLength < length && i < text.length) {
    const char = text[i];
    result.push(char);

    if (['i', 'l'].includes(char)) {
      currLength += 0.3;
    } else if (char.match(/[a-z0-9_-]/)) {
      currLength += 0.5;
    } else if (char.match(/[A-Z]/)) {
      currLength += 0.6;
    } else {
      currLength += 1;
    }

    i++;
  }

  if (i === text.length - 1 && length + 2 > currLength) {
    result.push(text[i]);
  }

  const finalStr = result.join('');
  return finalStr === text ? finalStr : finalStr + '...';
}
