/**
 * @file 方法用于加载 Monaco-editor
 * @author 五灵
 */
import { getCurrentLang, LangEnum } from '../utils/I18N';
const load = require('load-script');

const loadEditor = () => {
  return new Promise<boolean>((resolve, reject) => {
    const monacoPath = window['monacoConfUrl'] || '//g.alicdn.com/nel/dataphin/7.7.7/monaco';
    const lang = getCurrentLang();
    load(`${monacoPath}/vs/loader.js`, (err: any) => {
      if (!err) {
        window['require'].config({
          baseUrl: monacoPath,
          paths: {
            vs: `vs`,
            SQLWorker: `SQLWorker`,
            ShellWorker: 'ShellWorker',
            'vscode-languageserver-types': `main`,
            chevrotain: 'chevrotain.min',
            definitions: `definitions`,
            'sql-formatter': `sql-formatter`,
            'intellij-parser': `intellij-parser`,
            lodash: `lodash`,
            'tools/autofix/SymSpell': 'tools/autofix/SymSpell',
            'tools/autofix/sqlDict': 'tools/autofix/sqlDict',
            'tools/autofix/shellDict': 'tools/autofix/shellDict',
          },
          // 'vs/nls': {
          //   /** 加载对应的中英文语言包 */
          //   availableLanguages: { '*': lang === LangEnum.zh_CN ? 'zh-cn' : '' },
          // },
        });
        window['require'](['vs/editor/editor.main'], () => {
          resolve();
        });
      } else {
        reject(err);
      }
    });
  });
};

export default loadEditor;

