/**
 * @file 已选中的筛选条件列表
 * @author 会影， 晟朗
 */
import * as React from 'react';
import * as _ from 'lodash';
import { GroupOption, FilterDisplayProps } from './define';
import * as classNames from 'classnames';
import { getCheckedFilterOptionsByData } from './utils';
import { Abbr } from '../Abbr/Abbr';
import { Icon, Input } from 'antd';
import I18N from '../utils/I18N';
const Search = Input.Search;
/**
 * 展示选中的筛选器列表
 */
class FilterDisplay extends React.Component<FilterDisplayProps, any> {
  static defaultProps: Partial<FilterDisplayProps> = {
    always: [],
  };
  /**
   * 处理筛选的删除逻辑
   */
  handleDelete = (option: GroupOption, index: number) => {
    /** 这里对内部属性修改，层级较深，无法简单解构构建新对象 */
    let checkedData = _.cloneDeep(this.props.checkedData);
    const entry = _.find(checkedData, item => {
      return item.id === option.groupId;
    });
    if (entry.options.length < 2) {
      // 如果只有一个option, 去除整个entry
      checkedData = checkedData.filter(o => o.id !== option.groupId);
    } else {
      entry.options = entry.options.filter(opt => opt.value !== option.value);
    }
    this.props.onChange && this.props.onChange({ option, index, checkedData });
  };
  /**
   * 渲染筛选下拉列表
   */
  renderOptions() {
    const { checkedData, data, always } = this.props;
    const checkedOptions = getCheckedFilterOptionsByData(data, checkedData);

    return checkedOptions.map((option: GroupOption, index: number) => {
      return (
        <div className="filter-option" key={index}>
          <div>
            {`${option.groupDes}${I18N.common.punctuation.colon}  `}
            <Abbr text={option.des} length={9} />
          </div>
          {!always.includes(option.groupId) && (
            <div className="filter-option-close" onClick={this.handleDelete.bind(this, option, index)}>
              <Icon type="close" />
            </div>
          )}
        </div>
      );
    });
  }

  render() {
    const { className, value, onSearch, placeholder, hideSearch } = this.props;
    const classes = classNames(className, 'oned-filter-display');

    return (
      <div className={classes}>
        {!hideSearch && (
          <Search
            placeholder={placeholder || I18N.FilterGroup.keywordPlaceholder}
            style={{ width: 175, marginRight: 10 }}
            value={value}
            onChange={onSearch}
          />
        )}
        {this.renderOptions()}
      </div>
    );
  }
}

export default FilterDisplay;

