/**
 * @file 消息中心
 * @author wb-zwj313980
 */

import * as React from 'react';
import * as _ from 'lodash';
import I18N from '../../utils/I18N';
import { Icon } from '../../Icon';
import { Badge, Drawer, Button, Tabs, Spin, Popconfirm, Icon as AntdIcon, Modal } from 'antd';
import { MessageTab, ApplyStatus, MessageBO, ApplyDetailModel } from './definition';
import ApprovalList from './ApprovalList';
import ApprovalDetailModal from './ApprovalDetailModal';
import Empty from '../../Nav/Empty';

const { TabPane } = Tabs;

class Props {
  /** 待我审批列表 */
  approvalData?: MessageBO = { data: [] };
  /** 我的申请 */
  applicationData?: MessageBO = { data: [] };
  /** tab切换 */
  onMessageTabChange?: (tab: MessageTab) => void;
  /** 同意申请 */
  onAgreeApproval?: (apply: ApplyDetailModel) => Promise<any>;
  /** 拒绝申请 */
  onRefuseApproval?: (apply: ApplyDetailModel) => Promise<any>;
  /** 撤销申请 */
  onCancelApproval?: (apply: ApplyDetailModel) => Promise<any>;
}

interface State {
  /** 消息列表窗口 */
  messageDrawerVisible: boolean;
  /** 消息详情弹窗 */
  messageDetailModalVisible: boolean;
  /** 列表tab类型 */
  messageTab: MessageTab;
  /** 当前消息详情 */
  currApprovalDetail: ApplyDetailModel;
  /** 撤销申请ID */
  cancelConfirmModalVisable: boolean;
  /** 同意按钮 loading */
  agreeApprovalLoading: boolean;
  /** 拒绝按钮 loading */
  refuseApprovalLoading: boolean;
  /** 撤销按钮 loading */
  cancelApprovalLoading: boolean;
}

class MessageNotification extends React.Component<Props, State> {
  static defaultProps = new Props();
  state = {
    messageDrawerVisible: false,
    messageDetailModalVisible: false,
    messageTab: MessageTab.Pending,
    currApprovalDetail: null,
    cancelConfirmModalVisable: false,
    agreeApprovalLoading: false,
    refuseApprovalLoading: false,
    cancelApprovalLoading: false,
  };

  /** 切换列表类型 */
  handleChangeMessageTab = (messageTab: MessageTab) => {
    const { onMessageTabChange } = this.props;
    this.setState({
      messageTab,
    });
    if (onMessageTabChange) {
      onMessageTabChange(messageTab);
    }
  };

  /** 打开关闭消息窗口 */
  handleChangeMessageDrawerVisible = () => {
    const { messageDrawerVisible } = this.state;
    this.setState({
      messageDrawerVisible: !messageDrawerVisible,
    });
  };

  /** 点击审批详情 打开详情弹窗 */
  handleOpenApprovalDetail = (approval: ApplyDetailModel) => {
    this.setState({
      messageDetailModalVisible: true,
      currApprovalDetail: approval,
    });
  };

  /** 关闭审批详情 */
  handleClooseApprovalDetail = () => {
    this.setState({
      messageDetailModalVisible: false,
      currApprovalDetail: null,
    });
  };

  /** 打开撤销申请的警告弹窗 */
  handleOpenCancelConfirmModal = (model: ApplyDetailModel) => {
    this.setState({
      cancelConfirmModalVisable: true,
      currApprovalDetail: model,
    });
  };

  /** 关闭撤销申请的警告弹窗 */
  handleClooseCancelConfirmModal = () => {
    this.setState({
      cancelConfirmModalVisable: false,
      currApprovalDetail: null,
    });
  };

  commonPromiseAction = (promiseName: string, loadingStateName: keyof State) => {
    const { currApprovalDetail } = this.state;
    const promiseFun = this.props[promiseName];
    if (!promiseFun || typeof promiseFun !== 'function') {
      return;
    }
    const res = promiseFun(currApprovalDetail);
    if (res && res.then) {
      this.setState({
        [loadingStateName]: true,
      } as any);
      res
        .then(() => {
          this.setState({
            [loadingStateName]: false,
            messageDetailModalVisible: false,
            currApprovalDetail: null,
            cancelConfirmModalVisable: false,
          } as any);
        })
        .catch(() => {
          this.setState({
            [loadingStateName]: false,
          } as any);
        });
    } else {
      this.setState({
        messageDetailModalVisible: false,
        currApprovalDetail: null,
        cancelConfirmModalVisable: false,
      });
    }
  };

  /** 点击同意申请 */
  handleClickAgreeApproval = () => {
    this.commonPromiseAction('onAgreeApproval', 'agreeApprovalLoading');
  };

  /** 点击拒绝申请 */
  handleClickRefuseApproval = () => {
    this.commonPromiseAction('onRefuseApproval', 'refuseApprovalLoading');
  };

  /** 撤销自己的申请 */
  handleClickCancelApproval = () => {
    this.commonPromiseAction('onCancelApproval', 'cancelApprovalLoading');
  };

  /** 渲染列表 */
  renderApprovalList = (type: MessageTab, content: MessageBO, extraProps: any) => {
    const data = _.get(content, 'data') || [];
    const loading = _.get(content, 'loading') || false;
    const tips =
      type === MessageTab.Pending
        ? I18N.QaNav.messageNotification.noPendingApprovalTips
        : I18N.QaNav.messageNotification.noMyApprovalTips;
    return data.length === 0 && !loading ? (
      <Empty imgUrl="https://img.alicdn.com/tfs/TB1V5EyfK3tHKVjSZSgXXX4QFXa-240-165.png" text={tips} />
    ) : (
      <ApprovalList type={type} data={data} {...extraProps} />
    );
  };

  renderCancelApprovalModal = () => {
    const { cancelConfirmModalVisable, cancelApprovalLoading } = this.state;

    return (
      <Modal
        wrapClassName="oned-qa-nav-cancel-approval-modal"
        title={I18N.QaNav.messageNotification.cancelApprovalTitle}
        visible={cancelConfirmModalVisable}
        onCancel={this.handleClooseCancelConfirmModal}
        onOk={this.handleClickCancelApproval}
        confirmLoading={cancelApprovalLoading}>
        <div className="confirm-wrapper">
          <AntdIcon className="confirm-icon" type="exclamation-circle" />
          <span className="confirm-text">{I18N.QaNav.messageNotification.cancelApprovalTips}</span>
        </div>
      </Modal>
    );
  };

  render() {
    const { approvalData, applicationData } = this.props;
    const {
      messageDrawerVisible,
      messageDetailModalVisible,
      messageTab,
      currApprovalDetail,
      agreeApprovalLoading,
      refuseApprovalLoading,
      cancelApprovalLoading,
    } = this.state;
    const tipMessages = _.get(approvalData, 'data');
    return (
      <div className="oned-qa-nav-message-notification">
        <Badge dot={_.isArray(tipMessages) && tipMessages.length > 0}>
          <span onClick={this.handleChangeMessageDrawerVisible}>
            <Icon type="oned-icon icon-news-specia icon-message-notification" />
          </span>
        </Badge>
        <Drawer
          width={600}
          className="oned-qa-nav-message-notification-drawer"
          title={I18N.QaNav.messageNotification.messageCenter}
          visible={messageDrawerVisible}
          onClose={this.handleChangeMessageDrawerVisible}>
          <Tabs
            animated={false}
            activeKey={messageTab}
            size="large"
            onChange={(key: string) => this.handleChangeMessageTab(key as MessageTab)}>
            <TabPane tab={I18N.QaNav.messageNotification.approvalPending} key={MessageTab.Pending}>
              {/* no set undefined,Spin defaultProps spinning is true, */}
              <Spin spinning={!!approvalData.loading}>
                <div className="message-list pending">
                  {this.renderApprovalList(MessageTab.Pending, approvalData, {
                    onClickDetail: this.handleOpenApprovalDetail,
                  })}
                </div>
              </Spin>
            </TabPane>
            <TabPane tab={I18N.QaNav.messageNotification.myApproval} key={MessageTab.Me}>
              <Spin spinning={!!applicationData.loading}>
                <div className="message-list my">
                  {this.renderApprovalList(MessageTab.Me, applicationData, {
                    onClickDetail: this.handleOpenApprovalDetail,
                    onCancel: this.handleOpenCancelConfirmModal,
                  })}
                </div>
              </Spin>
            </TabPane>
          </Tabs>
        </Drawer>
        <ApprovalDetailModal
          visible={messageDetailModalVisible}
          detail={currApprovalDetail}
          onCancel={this.handleClooseApprovalDetail}
          footer={
            messageTab === MessageTab.Pending ? (
              <div>
                <Button
                  onClick={this.handleClickRefuseApproval}
                  loading={refuseApprovalLoading}
                  disabled={agreeApprovalLoading}>
                  {I18N.QaNav.messageNotification.refuseApproval}
                </Button>
                <Button
                  type="primary"
                  onClick={this.handleClickAgreeApproval}
                  disabled={refuseApprovalLoading}
                  loading={agreeApprovalLoading}>
                  {I18N.QaNav.messageNotification.agreeApproval}
                </Button>
              </div>
            ) : (
              <div>
                {_.get(currApprovalDetail, 'flagStatus') === ApplyStatus.PENDING && (
                  <Popconfirm
                    overlayClassName="oned-qa-nav-cancel-approval-pop"
                    title={I18N.QaNav.messageNotification.cancelApprovalTips}
                    icon={<AntdIcon type="exclamation-circle" />}
                    onConfirm={this.handleClickCancelApproval}>
                    <Button loading={cancelApprovalLoading}>{I18N.QaNav.messageNotification.cancelApproval}</Button>
                  </Popconfirm>
                )}
                <Button onClick={this.handleClooseApprovalDetail}>{I18N.QaNav.messageNotification.cloose}</Button>
              </div>
            )
          }
        />
        {this.renderCancelApprovalModal()}
      </div>
    );
  }
}

export default MessageNotification;

