/**
 * @file 帮助中心
 * @author wb-zwj313980
 */

import * as React from 'react';
import * as _ from 'lodash';
import { Tooltip } from 'antd';
import { Icon } from '../../Icon';
import I18N from '../../utils/I18N';

export interface HelpCenterLi {
  /** 帮助列表项icon */
  icon: React.ReactNode;
  /** 帮助列表项描述 */
  text: string;
  /** 帮助列表项链接 */
  href?: string;
  /** 点击事件 */
  onClick?: (e: React.MouseEvent, item: HelpCenterLi) => void;
}

class HelpCenterProps {
  /** 帮助中心标题 */
  helpCenterTitle? = I18N.QaNav.helpCenterTitle;
  /** 帮助中心列表 */
  helpCenterList?: HelpCenterLi[] = [];
}

export default class HelpCenter extends React.Component<HelpCenterProps> {
  static defaultProps = new HelpCenterProps();

  /** 点击事件 */
  handleClickHelpItem = (e: React.MouseEvent, item: HelpCenterLi) => {
    const { onClick } = item;
    if (onClick) {
      onClick(e, item);
    }
  };

  render() {
    const { helpCenterTitle, helpCenterList } = this.props;
    return _.isArray(helpCenterList) && helpCenterList.length > 0 ? (
      <Tooltip
        arrowPointAtCenter
        getPopupContainer={() => document.querySelector('.oned-qa-help-center')}
        placement="bottomRight"
        title={
          <div className="help-center">
            <div className="help-title">{helpCenterTitle}</div>
            <div className="help-list">
              {helpCenterList.map(item => (
                <a
                  className="help-item"
                  href={item.href}
                  onClick={e => {
                    this.handleClickHelpItem(e, item);
                  }}>
                  <span className="help-item-icon">{item.icon}</span>
                  <span>{item.text}</span>
                </a>
              ))}
            </div>
          </div>
        }>
        <span className="oned-qa-help-center">
          <Icon type="oned-icon icon-help-special help-center-icon" />
        </span>
      </Tooltip>
    ) : null;
  }
}

