/**
 * @file Diff编辑器
 * @author 晟朗
 * @description 用于不同版本的代码进行比较
 */

import * as React from 'react';
import { EditorLanguages } from './define';
import { LoadMonacoPromise } from './MonacoEditor';

interface Props {
  /** 用于对比的数据，顺序为modified, original */
  data: [string, string];
  /** 编辑器应用的语言 */
  language: EditorLanguages;
}

class DiffEditor extends React.Component<Props> {
  diffEditor;
  node;

  componentDidMount() {
    LoadMonacoPromise.then(
      () => {
        this.updateContent(this.props.data);
      },
      err => {
        console.log(err, 'err');
      }
    );
  }

  componentWillReceiveProps(nextProps: Props) {
    if (this.diffEditor) {
      this.updateContent(nextProps.data);
    }
  }

  componentWillUnmount() {
    if (this.diffEditor) {
      this.diffEditor.getModel().original.dispose();
      this.diffEditor.getModel().modified.dispose();
      this.diffEditor.dispose();
    }
  }

  updateContent(data: string[]) {
    const { language } = this.props;
    const currentModel = monaco.editor.createModel(data[0], language);
    const lastModel = monaco.editor.createModel(data[1], language);

    this.diffEditor =
      this.diffEditor || monaco.editor.createDiffEditor(this.node, { theme: 'darkTheme', readOnly: true });
    this.diffEditor.setModel({
      modified: currentModel,
      original: lastModel,
    });
  }

  render() {
    return <div className="oned-diff-editor-container" ref={ref => (this.node = ref)} />;
  }
}

export { DiffEditor };
