import * as moment from 'moment';
import { getCurrentLang, LangEnum } from '../../utils/I18N';

/** 格式化日期 */
export const formatTime = (date: moment.Moment | string, format?: string) => {
  if (!date) {
    return '';
  }
  const defaultDateFormat = getCurrentLang() === LangEnum.en_US ? 'MMM DD, YYYY, HH:mm:ss' : 'YYYY-MM-DD HH:mm:ss';
  return moment(date).format(format || defaultDateFormat);
};
