import { GroupOption } from './define';

/**
 * 返回选中的值的另一种格式，格式 [{ groupId,  groupDes, des, value, count, checked }]
 */
function getCheckedFilterOptionsByData(data, checkedData): GroupOption[] {
  const mergedData = [];
  checkedData.forEach(o => {
    const entry = data.find(j => j.id === o.id);
    if (entry) {
      const optionValues = o.options.map(k => k.value);
      mergedData.push({
        id: entry.id,
        des: entry.des,
        options: entry.options.filter(l => optionValues.indexOf(l.value) > -1),
      });
    }
  });
  const result = [];
  mergedData.map(o => {
    (o.options || []).map(checkedOption => {
      result.push({
        ...checkedOption,
        groupId: o.id,
        groupDes: o.des,
      });
    });
  });
  return result;
}

/**
 * 根据子树根节点查找到对应子树
 * */
function findSubTree(root: any[], subRoot: string) {
  const queue = [root];
  let target = queue.shift();
  let ans = {};
  while (target && !Object.keys(ans).length) {
    Object.entries(target).forEach(([key, value]) => {
      if (key === subRoot) {
        ans = value;
      }
      queue.push(value);
    });
    target = queue.shift();
  }
  return ans;
}

