/**
 * 获取当前光标位置的单词
 */
export function getCurWordPosition(
  model: monaco.editor.IReadOnlyModel,
  position: monaco.IPosition
): monaco.editor.IWordAtPosition {
  return model.getWordAtPosition(position);
}
