import * as React from 'react';
import * as _ from 'lodash';
import I18N from '../../utils/I18N';
import { Modal, Timeline } from 'antd';
import { Icon } from '../../Icon';
import { Abbr } from '../../Abbr/Abbr';
import { formatTime } from './utils';
import { ApprovalTypeDes, ApplyStatusDes, ApplyStatus, ApplyDetailModel, DataBankProtocolField } from './definition';

interface ApprovalDetailModalProps {
  /** 显示隐藏 */
  visible: boolean;
  /** 详情 */
  detail: ApplyDetailModel;
  /** 弹窗 footer */
  footer: React.ReactNode;
  /** 关闭详情 */
  onCancel: () => void;
}

export default class ApprovalDetailModal extends React.Component<ApprovalDetailModalProps> {
  getApplyContent = () => {
    const { detail } = this.props;
    const applyContent = _.get(detail, 'applyContent') || {};
    try {
      return JSON.parse(`${applyContent}`);
    } catch {
      return {};
    }
  };

  render() {
    const { visible, detail, footer, onCancel } = this.props;
    const applicationType = _.get(detail, 'resourceType');
    const creatTime = _.get(detail, 'gmtCreate');
    const endTime = _.get(detail, 'gmtModified');
    const applicationCreateTime = creatTime ? formatTime(creatTime) : '';
    const applicationEndTime = endTime ? formatTime(endTime) : '';
    const applyStatus = _.get(detail, 'flagStatus') as ApplyStatus;
    const applyStatusDes = ApplyStatusDes[applyStatus];
    const applyContent = this.getApplyContent();
    return (
      <Modal
        destroyOnClose
        wrapClassName="oned-qa-nav-approval-detail-modal"
        width={600}
        title={I18N.QaNav.messageNotification.approvalDetail}
        visible={visible}
        onCancel={onCancel}
        footer={footer}>
        <div className="approval-detail">
          <div className="approval-detail-item">
            <span>{I18N.QaNav.messageNotification.approvalID}</span>
            <span>
              <Abbr text={_.get(detail, 'applyId') || ''} line={1} />
            </span>
          </div>
          <div className="approval-detail-item">
            <span>{I18N.QaNav.messageNotification.applicationType}</span>
            <span>
              <Abbr text={_.get(ApprovalTypeDes, `${applicationType}`) || ''} line={1} />
            </span>
          </div>
          <div className="approval-detail-item">
            <span>{I18N.QaNav.messageNotification.applicant}</span>
            <span>
              <Abbr text={_.get(detail, 'applicantName') || ''} line={1} />
            </span>
          </div>
          <div className="approval-detail-item">
            <span>{I18N.QaNav.messageNotification.applicationTime}</span>
            <span>{applicationCreateTime}</span>
          </div>
          <div className="approval-detail-item approval-detail-content">
            <span>{I18N.QaNav.messageNotification.approvalDetail}</span>
            <div className="content">
              {DataBankProtocolField.slice(0, 3).map(field => (
                <div className="approval-detail-item" key={field.key}>
                  <span>{field.label}</span>
                  <span>
                    <Abbr text={_.get(applyContent, field.key) || ''} line={1} />
                  </span>
                </div>
              ))}
            </div>
          </div>
          {applyStatusDes && (
            <div className="approval-detail-item approval-time-progress">
              <span>{I18N.QaNav.messageNotification.approvalProgress}</span>
              <div className="time-progress">
                <Timeline>
                  <Timeline.Item dot={<Icon type="oned-icon icon-success success" />}>
                    <div className="time-point">
                      <span className="applicant">
                        <Abbr text={_.get(detail, 'applicantName') || ''} length={10} />
                        <span>{I18N.QaNav.messageNotification.openApprovalProgress}</span>
                      </span>
                      <span className="time">
                        <span>{applicationCreateTime}</span>
                      </span>
                    </div>
                  </Timeline.Item>
                  <Timeline.Item dot={<Icon type={`oned-icon ${applyStatusDes.icon} ${applyStatusDes.className}`} />}>
                    <div className="time-point">
                      {applyStatus === ApplyStatus.PENDING ? (
                        <span className="applicant">
                          <Abbr
                            text={((_.get(detail, 'admins') as any) || []).join(I18N.common.punctuation.dayton)}
                            length={20}
                          />
                          <span>{applyStatusDes.text}</span>
                        </span>
                      ) : (
                        <>
                          <span className="applicant">
                            <Abbr text={_.get(detail, 'approverName', '')} length={10} />
                            <span>{applyStatusDes.text}</span>
                          </span>
                          <span className="time">
                            <span>{applicationEndTime}</span>
                          </span>
                        </>
                      )}
                    </div>
                  </Timeline.Item>
                </Timeline>
              </div>
            </div>
          )}
        </div>
      </Modal>
    );
  }
}

