/**
 * @file 用户头像组件
 * @author cherry.zj
 */

import * as React from 'react';
import { Abbr } from '../../Abbr/Abbr';
import { Dropdown } from 'antd';
import { Icon } from '../../Icon';
import I18N from '../../utils/I18N';
import * as _ from 'lodash';

enum RoleCode {
  /** 管理员 */
  ADMIN = 'ROLE_ES_ADMIN',
  /** 开发者 */
  DEVELOPER = 'ROLE_ES_DEVELOPER',
  /** 分析师 */
  ANALYSTS = 'ROLE_ES_ANALYSTS',
}

export class UserInfo {
  /** 用户名 */
  accountName = 'Quick Audience';
  /** 昵称 */
  nickName = '分析师';
  /** 是否为管理员 */
  isAdmin = false;
  /** 角色名称 */
  roleName = 'ANALYSTS';
  /** 角色code */
  roleCode = RoleCode.ANALYSTS;
}

export interface Instance {
  /** 版本名称 */
  versionName: string;
  /** 续费链接 */
  renewUrl: string;
  /** 还剩多少天到期 */
  remainDay: number;
}

class AvatarProps {
  /** 用户名 */
  userInfo = new UserInfo();
  /** 头像地址 */
  img?: string;
  /** 额外的 className */
  className?: string;
  /** 用户实例 */
  instance?: Instance;
}

export default class Avatar extends React.Component<AvatarProps, any> {
  static defaultProps = new AvatarProps();

  getIconType = () => {
    const { userInfo } = this.props;
    const roleCode = userInfo.roleCode;

    switch (roleCode) {
      case RoleCode.ADMIN:
        return {
          iconStyle: 'icon-guanliyuan',
          colorStyle: 'avatar-guan-color',
        };
      case RoleCode.DEVELOPER:
        return {
          iconStyle: 'icon-kaifazhe',
          colorStyle: 'avatar-kai-color',
        };
      case RoleCode.ANALYSTS:
        return {
          iconStyle: 'icon-fenxizhe',
          colorStyle: 'avatar-fen-color',
        };
      default:
        return null;
    }
  };

  render() {
    const { userInfo, img, className, instance } = this.props;
    const roleName = userInfo.roleName;
    const iconStyle = _.get(this.getIconType(), 'iconStyle', '');
    const colorStyle = _.get(this.getIconType(), 'colorStyle', '');

    return (
      <Dropdown
        placement="bottomRight"
        overlay={
          <div className="qa-avatar-dropdown">
            <div className="avatar-card">
              <img src="https://img.alicdn.com/tfs/TB1JQxotDtYBeNjy1XdXXXXyVXa-818-818.png" />
              <div className="avatar-detail">
                <Abbr text={userInfo.nickName} line={1} />
                <Abbr text={userInfo.accountName} line={1} />
              </div>
              <div className="avatar-role">
                <Icon type={`oned-icon ${iconStyle}`} className={`avatar-icon ${colorStyle}`} />
                <span className="avatar-name">{roleName}</span>
              </div>
            </div>
            {instance && (
              <div className="version-info">
                <div className="version-wrapper">
                  <span className="version">
                    <Icon type="oned-icon icon-vip-special version-icon" />
                    <span className="des">{instance.versionName}</span>
                  </span>
                  <span>
                    {I18N.template(I18N.QaNav.avatar.vipRemainDay, {
                      val: instance.remainDay,
                    })}
                  </span>
                </div>
                <a className="renewal" href={instance.renewUrl}>
                  {I18N.QaNav.avatar.renewal}
                </a>
              </div>
            )}
            <a className="operating-item" target="_self" href="/logout">
              <Icon type="oned-icon icon-sign-out" />
              {I18N.QaNav.avatar.exitAccount}
            </a>
          </div>
        }>
        {img ? (
          <img src={img} className={`qa-avatar-img ${className}`} alt={name} />
        ) : (
          <span className={`qa-avatar-text ${className}`}>{(userInfo.nickName || '-').slice(0, 1)}</span>
        )}
      </Dropdown>
    );
  }
}

