/**
 * @file 浮层显示的筛选条件组
 * @author 会影， 晟朗
 */
import * as React from 'react';
import * as classNames from 'classnames';
import { TooltipPlacement } from 'antd/lib/tooltip/index';
import { FilterGroupProps } from './define';
import FilterGroup from './FilterGroup';
import { Popover, Button, Icon } from 'antd';
import I18N from '../utils/I18N';

interface FilterGroupPopoverProps extends FilterGroupProps {
  /** 按钮上提示文案位置 */
  placement?: TooltipPlacement;
  /** 按钮上显示的文案 */
  buttonText?: string;
}

class FilterGroupPopover extends React.Component<FilterGroupPopoverProps, any> {
  static defaultProps: Partial<FilterGroupPopoverProps> = {
    placement: 'bottomLeft',
    buttonText: I18N.FilterGroup.filter,
  };

  render() {
    const {
      className,
      placement,
      data,
      checkedData,
      buttonText,
      onChange,
      relation,
      mode,
      searchGroupIds,
      onSearch,
    } = this.props;
    const containerClasses = classNames('oned-filter-group-popover-container', className);
    const triggerClasses = classNames('filter-group-popover-trigger', className);

    const popoverContent = (
      <div className={containerClasses}>
        <FilterGroup
          data={data}
          checkedData={checkedData}
          onChange={onChange}
          relation={relation}
          mode={mode}
          searchGroupIds={searchGroupIds}
          onSearch={onSearch}
        />
      </div>
    );

    return (
      <Popover placement={placement} content={popoverContent} trigger="hover">
        <Button type="primary" className={triggerClasses}>
          {buttonText}
          <Icon type="down" />
        </Button>
      </Popover>
    );
  }
}

export default FilterGroupPopover;

