/**
 * @file 智能编辑器
 * @author 晟朗
 * @description 支持ODPSSQL， HiveSQL，Shell
 */
import * as React from 'react';
import * as _ from 'lodash';
import loadEditor from './LoadMonaco';
import { EditorLanguages, CompletionItemKind } from './define';
import { WhiteTheme, DarkTheme } from './Theme';
import { getCurWordPosition } from './SqlUtils';

export interface HoverContent {
  /** 元素名称 */
  name: string;
  /** 调用格式，UDF需要 */
  commandHelp?: string;
  /** 元素描述信息 */
  description: string;
}

interface Query {
  /** SQL表信息，字段信息查询接口，需要支持keyword参数 */
  url: string;
  /** 其他接口参数，仅支持query参数 */
  params?: {
    [key: string]: string | number;
  };
}

export enum ItemType {
  UDF = 'UDF',
}

export interface EditorProps {
  /** 编辑器内容 */
  value: string;
  /** 编辑器内容变化时的回调 */
  onValueChange: Function;
  /** 编辑器应用的语言 */
  language: EditorLanguages;
  /** 编辑器内置配置 */
  options?: object;
  /** 编辑器实例创建成功的回调 */
  onEditorCreated?: Function;
  /** 是否禁用编辑 */
  disabled?: boolean;
  /** 元数据查询接口 */
  query?: Query;
  /** SQL编辑器左侧的错误标记 */
  marks?: Array<{
    startRow: number;
    startCol: number;
    message: string;
  }>;
  /** 元数据类型Enum，控制Icon和类型信息 */
  completionTypes: {
    [key: string]: {
      text: string;
      kind: CompletionItemKind;
    };
  };
  /** 是否退化，原子指标，业务限定需要支持sql碎片，无法正常构建AST，退化为基于输入对表名查找 */
  degenerate?: boolean;
  /** UDF列表 */
  UDFList: Array<{ id: number; name: string }>;
  /** hover UDF时的回调 */
  onHover: (type: ItemType, id: number) => HoverContent;
  /** 静态资源加载路径，默认加载cdn */
  baseUrl?: string;
}

/** requirejs会静默处理重复加载已经加载过的资源，导致loadEditor无法resolve，所以这里采用同步形式 */
const LoadMonacoPromise = loadEditor().then(() =>
  import(/* webpackChunkName: "monaco-chunk" */
  /* webpackMode: "lazy" */
  'fuse-editor')
);

let inited = false;

export { LoadMonacoPromise };

class MonacoEditor extends React.PureComponent<EditorProps, {}> {
  node: HTMLElement;
  editor: monaco.editor.IStandaloneCodeEditor & { cleanCache: () => void };
  fuse;
  deltaDecorations: string[];

  generateFuseOptions(language: EditorLanguages, props: EditorProps) {
    const { query, UDFList, onHover, degenerate, completionTypes } = props;
    if (language === EditorLanguages.Shell) {
      return {
        autofix: false,
      };
    }
    return {
      query,
      udf: UDFList,
      onHover,
      degenerate,
      autofix: false,
      completionTypes,
    };
  }

  initEditor = async () => {
    const { language } = this.props;

    if (!inited) {
      inited = true;
      monaco.editor.defineTheme('whiteTheme', WhiteTheme);
      monaco.editor.defineTheme('darkTheme', DarkTheme);

      /** 由于editor仅会在didMount时初始化，但是此时获得的languageId不一定是准确的，因此第一次初始化时注册所有语言 */
      await this.fuse.init(language, this.generateFuseOptions(language, this.props));
    }
  };

  createEditor = () => {
    const { language } = this.props;

    this.fuse.updateOptions(this.generateFuseOptions(language, this.props));

    // 去掉为 undefined 的 options
    const options = _.pickBy(this.props.options, value => {
      return value !== undefined;
    });
    if (!this.node) {
      return;
    }

    this.editor = monaco.editor.create(this.node, {
      theme: 'darkTheme',
      value: this.props.value,
      language,
      suggestLineHeight: 24,
      automaticLayout: true,
      minimap: {
        enabled: false,
      },
      fontSize: 14,
      lineHeight: 20,
      folding: false,
      wordWrap: 'on',
      lineDecorationsWidth: 0,
      lineNumbersMinChars: 3,
      readOnly: this.props.disabled,
      ...options,
    }) as any;

    if (this.props.onEditorCreated) {
      this.props.onEditorCreated(this.editor);
    }
    this.editor.onDidChangeModelContent((evt: monaco.editor.IModelContentChangedEvent) => {
      const value = this.editor.getValue();
      if (this.props.onValueChange) {
        this.props.onValueChange(value);
      }
    });
  };

  formatter() {
    if (this.editor) {
      this.editor.getAction('editor.action.formatDocument').run();
      return this.editor.getValue();
    }

    return '';
  }

  cleanCache() {
    if (this.fuse) {
      this.fuse.cleanCache();
    }
  }

  setScrollPosition(params: monaco.editor.INewScrollPosition) {
    if (this.editor) {
      this.editor.setScrollPosition(params);
    }
  }

  setMonacoValue(editor: monaco.editor.IStandaloneCodeEditor, newValue: string) {
    editor.setValue(newValue);
  }

  componentDidMount() {
    LoadMonacoPromise.then(async fuse => {
      this.fuse = fuse;
      await this.initEditor();
      await this.createEditor();
    });
  }

  componentWillUnmount() {
    if (this.editor) {
      this.editor.getModel().dispose();
      this.editor.dispose();
    }
  }

  componentWillReceiveProps(nextProps: EditorProps) {
    const { value, disabled, marks } = this.props;
    const { options, language } = nextProps;
    /** monaco 目前不支持多种 theme 共存，目前　Hack　一下切换theme */
    /** https://github.com/Microsoft/monaco-editor/issues/338 */
    if (this.editor) {
      monaco.editor.setModelLanguage(this.editor.getModel(), language);
      /** 切换tab之后对于monaco-editor的示例要更新envirenment和designtable属性，对于同一个tab下的编辑器来说，两者一直不变，所以可以使用this.props */
      this.fuse.updateOptions(this.generateFuseOptions(language, nextProps));
    }

    if (this.editor && nextProps.value !== value) {
      const editorValue = this.editor.getValue();
      if (nextProps.value !== editorValue) {
        this.setMonacoValue(this.editor, nextProps.value || '');
      }
    }
    if (this.editor && nextProps.disabled !== disabled) {
      this.editor.updateOptions({ readOnly: nextProps.disabled });
    }
    if (!_.isEqual(nextProps.marks, marks)) {
      this.showErrorMsg(nextProps.marks);
    }
  }
  /**
   * 显示编辑器的错误信息
   */
  showErrorMsg(marks: any[]) {
    const { language } = this.props;
    let markers = [];
    const decorations = [];
    marks.map(mark => {
      const curWord = getCurWordPosition(this.editor.getModel(), {
        lineNumber: mark.startRow,
        column: mark.startCol,
      });
      const marker: monaco.editor.IMarkerData = {
        code: '0xFFFF0001',
        message: mark.message,
        startLineNumber: mark.startRow,
        endLineNumber: mark.startRow,
        startColumn: curWord && curWord.startColumn,
        endColumn: curWord && curWord.endColumn,
        severity: monaco.Severity.Error,
      };
      const decoration = {
        range: new monaco.Range(mark.startRow, 1, mark.startRow, 1),
        options: {
          isWholeLine: false,
          glyphMarginHoverMessage: mark.message,
          glyphMarginClassName: 'decoration-glyph',
        },
      };
      markers.push(marker);
      decorations.push(decoration);
    });
    markers = _.uniqBy(markers, 'message');
    if (this.deltaDecorations) {
      this.deltaDecorations = this.editor.deltaDecorations(this.deltaDecorations, decorations);
    } else {
      this.deltaDecorations = this.editor.deltaDecorations([], decorations);
    }
    monaco.editor.setModelMarkers(this.editor.getModel(), language, markers);
  }

  render() {
    return (
      <div className="oned-editor-container">
        <div
          className="oned-editor"
          ref={node => {
            this.node = node as HTMLElement;
          }}
        />
      </div>
    );
  }
}

export { MonacoEditor };
