/**
 * @file 筛选条件组
 * @author 会影, 晟朗
 */
import * as React from 'react';
import * as _ from 'lodash';
import { GroupOption, FilterGroupProps, FilterGroupState } from './define';
import * as classNames from 'classnames';
import { Input, Icon, Tooltip } from 'antd';
import { Abbr } from '../Abbr/Abbr';
import I18N from '../utils/I18N';
import { findSubTree } from './utils';
const Search = Input.Search;

class FilterGroup extends React.Component<FilterGroupProps, FilterGroupState> {
  private FilterNum = 0;
  constructor(props: FilterGroupProps) {
    super(props);
    this.state = {
      localData: props.data,
      /** 每一类filter均可搜索，故为对象 */
      searchedValue: {},
      /** 每一类filter options的总长度是否超过页面宽度，超长时需要显示折叠箭头，并记录对应折叠的展开，收起状态 */
      hasFolder: [],
      /** 避免不同tab间多个实例之间状态污染 */
      filterNo: this.FilterNum++,
      /** filterGroup整体的展开，收起状态 */
      entriesStatus: false,
    };
  }

  componentDidMount() {
    this.setFolder();
    window.addEventListener('resize', this.throttleSetFolder);
  }
  componentWillReceiveProps(nextProps: FilterGroupProps) {
    if (!_.isEqual(nextProps.data, this.props.data)) {
      const { searchedValue } = this.state;
      let localData = nextProps.data;
      Object.entries(searchedValue).forEach(([key, value]) => {
        localData = this.defaultSearch(localData, value, key);
      });
      this.setState({
        localData,
      });
    }
  }
  componentDidUpdate(nextProps: FilterGroupProps, nextState: FilterGroupState) {
    if (!_.isEqual(nextState.localData, this.state.localData)) {
      this.setFolder();
    }
  }
  componentWillUnmount() {
    window.removeEventListener('resize', this.throttleSetFolder);
  }
  /**
   * 获取对应 Dom 元素宽度
   */
  getWidth(dom: Element) {
    return dom.getBoundingClientRect().width;
  }

  /**
   * 设置每一类Filter的折叠状态
   */
  setFolder = () => {
    const { hasFolder, filterNo } = this.state;
    document.querySelectorAll(`.filter-num-${filterNo} .group-options`).forEach((group, index) => {
      let totalLength = 0;
      group.querySelectorAll('.option-item').forEach(option => {
        totalLength += this.getWidth(option);
      });

      const entry = document.querySelector(`.filter-num-${filterNo} .group-entry`);
      const entryStyle = entry['currentStyle'] || window.getComputedStyle(entry);
      const effectiveWidth =
        entry['offsetWidth'] -
        Number(entryStyle.paddingLeft.match(/\d+/g)) -
        Number(entryStyle.paddingRight.match(/\d+/g)) -
        this.getWidth(document.querySelector('.group-des')) -
        this.getWidth(document.querySelector('.group-folder-arrow'));
      hasFolder[index] = {
        visible: totalLength > effectiveWidth,
        opened: totalLength > effectiveWidth ? (hasFolder[index] && hasFolder[index].opened ? true : false) : false,
      };
    });
    this.setState({
      hasFolder,
    });
  };

  /** 页面resize的时候，需要重新计算每一类filter options的总长度是否超长，超长时需要显示折叠箭头 */
  private throttleSetFolder = _.throttle(this.setFolder, 100);

  /** 修改某一类filter的折叠状态 */
  changeFolder = (e, index) => {
    const { hasFolder } = this.state;
    hasFolder[index] = {
      ...hasFolder[index],
      opened: !hasFolder[index].opened,
    };

    this.setState({
      hasFolder,
      entriesStatus: true,
    });
  };

  /** 将target中所有节点在source中的对应项删除 */
  peelOff = (target, source) => {
    let localSource = source;
    Object.keys(target).forEach(item => {
      const abandonedEntry = _.find(localSource, { id: item });
      const abandonedIndex = localSource.indexOf(abandonedEntry);
      if (abandonedIndex > -1) {
        localSource.splice(abandonedIndex, 1);
      }
      if (Object.keys(target[item]).length) {
        localSource = this.peelOff(target[item], localSource);
      }
    });
    return localSource;
  };

  /** filter group中同样可以点击option右下角的叉号清除选中状态 */
  handleDelete = (option: GroupOption, index: number, e: React.MouseEvent<HTMLLIElement>) => {
    e.stopPropagation();
    let checkedData = _.cloneDeep(this.props.checkedData);
    const entry = _.find(checkedData, item => {
      return item.id === option.groupId;
    });
    if (entry.options.length < 2) {
      // 如果只有一个option, 去除整个entry
      checkedData = checkedData.filter(o => o.id !== option.groupId);
    } else {
      entry.options = entry.options.filter(opt => opt.value !== option.value);
    }
    this.props.onChange && this.props.onChange({ option, index, checkedData });
  };

  /** 选中option */
  handleEntryClick = (entry, option: GroupOption, index: number) => {
    const { relation, mode, onChange } = this.props;
    // TODO 对于已经选中的标签，则反选
    const checkedData = _.cloneDeep(this.props.checkedData);
    let theEntry = _.find(checkedData, { id: entry.id });
    /** 某一类filter首次被选中时，新增一条记录，记录filter id和选中的options */
    if (!theEntry) {
      // 如果 entry 不存在，则添加
      theEntry = {
        id: entry.id,
        options: [],
      };
      checkedData.push(theEntry);
    }

    /** 某一类filter当前已经存在选中项，若为复选模式，则增加选项，若为单选模式，则更新选项 */
    if (!_.find(theEntry.options, { value: option.value })) {
      mode === 'multiply'
        ? theEntry.options.push({ value: option.value })
        : (theEntry.options = [{ value: option.value }]);
    }
    /** 如果外部传入了联动关系，则在上层option变化时，下层option选中状态均被清空 */
    if (relation) {
      this.peelOff(findSubTree(relation, entry.id), checkedData);
    }
    onChange &&
      onChange({
        checkedData,
        option: {
          ...option,
          groupId: entry.id,
          groupDes: entry.des,
        },
        index,
      });
  };

  /** 渲染每一类filter，一类占据一行，超长时隐藏options，并显示折叠箭头 */
  renderEntry = (entry, index: number) => {
    const { searchGroupIds, checkedData } = this.props;
    const canSearch = (searchGroupIds || []).includes(entry.id);
    const presentFolder = this.state.hasFolder[index] || {};
    const folderClass = classNames('group-folder', {
      'group-folder-opened': presentFolder.opened,
    });
    const folderArrowClass = classNames({
      'group-folder-arrow-i': presentFolder.opened,
    });

    return (
      <div className="group-entry" key={index}>
        <div className="group-des">{entry.des}</div>
        <div className="group-content">
          {canSearch && (
            <Input
              className="group-search"
              placeholder={I18N.FilterGroup.keyword}
              prefix={<Icon type="search" />}
              onChange={e => this.handleSearch(e.target.value, entry.id)}
            />
          )}
          <div className={folderClass}>
            <div className="group-options">
              {entry.options && entry.options.length ? (
                entry.options.map((o, i) => {
                  let isActive = false;
                  if (checkedData.find(e => e.id === entry.id)) {
                    isActive = !!(checkedData.find(e => e.id === entry.id) || []).options.find(
                      opt => opt.value === o.value
                    );
                  }

                  const classes = classNames('option-item', {
                    'filter-option': isActive,
                  });
                  return (
                    <span className={classes} key={i} onClick={this.handleEntryClick.bind(this, entry, o, i)}>
                      <Abbr text={o.des} length={9} />
                      {isActive && !(this.props.always || []).includes(o.groupId) && (
                        <div
                          className="filter-option-close"
                          onClick={this.handleDelete.bind(this, { ...o, groupId: entry.id }, i)}>
                          <Icon type="close" />
                        </div>
                      )}
                    </span>
                  );
                })
              ) : (
                <span className="option-placeholder">{I18N.FilterGroup.FoundNothing}</span>
              )}
            </div>
            <div className="group-folder-arrow">
              {presentFolder.visible && (
                <Icon type="down" className={folderArrowClass} onClick={e => this.changeFolder(e, index)} />
              )}
            </div>
          </div>
        </div>
      </div>
    );
  };

  /** 默认关键词搜索逻辑 */
  defaultSearch = (data: any[], value: {}, id) => {
    const selectedOptions = _.find(data, {
      id,
    })['options'].filter(item => {
      return item.des.indexOf(value) > -1;
    });
    const cloneData = _.cloneDeep(data);
    _.find(cloneData, { id })['options'] = selectedOptions;
    return cloneData;
  };

  /** 用户自定义搜索逻辑 */
  handleSearch = (value: string, id: string) => {
    // 用户传入onSearch方法时，执行用户逻辑，提供搜索id和查找值，否则执行默认搜索逻辑
    const { onSearch, data } = this.props;
    const { searchedValue } = this.state;
    searchedValue[id] = value;
    this.setState({
      searchedValue,
    });
    if (onSearch) {
      onSearch({ id, value });
    } else {
      let localData = data;
      Object.entries(searchedValue).forEach(([key, item]) => {
        localData = this.defaultSearch(localData, item, key);
      });
      this.setState({
        localData,
      });
    }
  };

  /** 切换filter group整体折叠状态 */
  changeEntries = () => {
    this.setState({
      entriesStatus: !this.state.entriesStatus,
    });
  };

  render() {
    const { className, onSearch } = this.props;
    const { localData, entriesStatus } = this.state;
    const classes = classNames(className, 'oned-filter-group', `filter-num-${this.state.filterNo}`);
    const entriesClasses = classNames({
      'group-entry-wrapper': !entriesStatus,
    });
    return (
      <div className={classes}>
        <div className={entriesClasses}>{localData.map(this.renderEntry)}</div>
        {localData.length > 1 && (
          <div className="filter-group-arrow">
            <Tooltip
              placement="bottom"
              title={entriesStatus ? I18N.FilterGroup.foldInFilters : I18N.FilterGroup.foldOutFilters}>
              <Icon type={entriesStatus ? 'up' : 'down'} onClick={this.changeEntries} />
            </Tooltip>
          </div>
        )}
      </div>
    );
  }
}

export default FilterGroup;

