/**
 * @file 全局顶部语言切换组件
 * @author 五灵
 */

import * as React from 'react';
import { Dropdown, Menu } from 'antd';
import { getCurrentLang, LangEnum } from '../../utils/I18N';

class LangSwitch extends React.Component<any, any> {
  lang: string;
  constructor(props) {
    super(props);
    this.lang = getCurrentLang();
  }
  changeLang = ({ key }) => {
    document.cookie = `es_lang=${key}; path=/`;
    document.cookie = `onedesign-locale=${key}; path=/`;
    document.cookie = `editor-locale=${key}; path=/`;
    window.location.reload();
  };
  render() {
    /* tslint:disable i18n */
    return (
      <Dropdown
        placement="bottomRight"
        overlay={
          <Menu onClick={this.changeLang}>
            {Object.keys(langText).map(lang => (
              <Menu.Item key={lang}>{langText[lang]}</Menu.Item>
            ))}
          </Menu>
        }>
        <div className="qa-lang-switch">
          <span>{langText[this.lang]}</span>
        </div>
      </Dropdown>
    );
  }
}

export default LangSwitch;

/* tslint:disable i18n */
const langText: Record<LangEnum, string> = {
  [LangEnum.zh_CN]: '中文（简体）',
  [LangEnum.zh_TW]: '中文（繁體）',
  [LangEnum.en_US]: 'English',
};

