/**
 * @file 通知盒子类型定义
 * @author 俞火江 & cherry.zj
 */

import * as moment from 'moment';

export enum NotificationType {
  Message = 'MESSAGE',
  Task = 'TASK',
}

export enum MessageSource {
  /** 全部 */
  ALL = 'ALL',
  /** 权限管理 */
  AUTH = 'AUTH',
  /** 流程控制 */
  BPMS = 'BPMS',
  /** 资产治理 */
  DATA_ASSETS = 'DATA_ASSETS',
}

interface MessageProfile {
  /** 内容 */
  content?: string;

  /** 消息等级，HIGH/MEDIUM/LOW */
  messageLevel?: string;

  /** 消息来源: DQE, VOLEMORT, OTHERS */
  messageSource?: string;

  /** 消息类型：SYSTEM/CUSTOMIZE */
  messageType?: string;

  /** 消息Url */
  messageUrl?: string;

  /** 标题 */
  title?: string;
}

export interface StationLetterDTO {
  /** 是否已读 */
  haveRead?: boolean;

  /** 消息内容 */
  messageProfile?: MessageProfile;

  /** 发送时间 */
  sendTime?: string;

  /** 站内信Id */
  stationLetterId?: number;

  /** 用户Id */
  userId?: string;
}

export interface ToBeHandleOaTaskBO {
  /** bizContent */
  bizContent?: string;

  /** createTime */
  createTime?: string;

  /** createUserId */
  createUserId?: string;

  /** createUserName */
  createUserName?: string;

  /** finishTime */
  finishTime?: string;

  /** name */
  name?: string;

  /** objectId */
  objectId?: number;

  /** objectType */
  objectType?: 'APPLY' | 'APPROVE';

  /** sourceSystem */
  sourceSystem?: string;

  /** status */
  status?: 'APPROVING' | 'ACCEPTED' | 'REJECTED' | 'REVOKED';

  /** type */
  type?: 'APPROVE' | 'MANAGE' | 'OTHERS';
}

export type Notification = StationLetterDTO | ToBeHandleOaTaskBO;

export interface NormalizedNotification {
  id: number;
  icon: string;
  sourceName: string;
  title: string;
  time: moment.Moment;
  url: string;
}
