/**
 * @file Nav
 * @author cherry.zj
 */

import * as React from 'react';
import { Icon as AntdIcon } from 'antd';
import { Icon } from '../Icon';
import LangSwitch from './LangSwitch';
import I18N from '../utils/I18N';
import Avatar, { UserInfo, Instance } from './Avatar';
import HelpCenter, { HelpCenterLi } from './HelpCenter';
import MessageNotification from './MessageNotification';
import { MessageBO, MessageTab, ApplyDetailModel } from './MessageNotification/definition';

interface NavBO {
  /** key值，也是pathname的值 */
  key: string;
  /** 名称 */
  title: string;
  /** 协议+服务器名+端口号，同window.location.origin属性 */
  origin?: string;
}

class Props {
  /** 协议+服务器名+端口号，同window.location.origin属性 */
  origin = window.location.origin;
  /** 用户信息 */
  userInfo = new UserInfo();
  /** 新增的导航栏 */
  navList = [] as NavBO[];
  /** 帮助中心选项 */
  helpCenterList?: HelpCenterLi[] = [];
  /** 帮助中心标题 */
  helpCenterTitle? = I18N.QaNav.helpCenterTitle;
  /** 用户实例 */
  instance?: Instance;
  /** 是否显示消息中心 */
  showMessageCenter?: boolean = true;
  /** 待我审批列表 */
  approvalData?: MessageBO = { data: [] };
  /** 我的申请列表 */
  applicationData?: MessageBO = { data: [] };
  /** tab切换 */
  onMessageTabChange?: (tab: MessageTab) => void;
  /** 同意申请回调 */
  onAgreeApproval?: (apply: ApplyDetailModel) => Promise<any>;
  /** 拒绝申请回调 */
  onRefuseApproval?: (apply: ApplyDetailModel) => Promise<any>;
  /** 撤销申请回调 */
  onCancelApproval?: (apply: ApplyDetailModel) => Promise<any>;
}

class QaNav extends React.Component<Props, any> {
  NavList = [
    {
      key: 'home',
      title: I18N.QaNav.Home,
    },
    {
      key: 'userSighting',
      title: I18N.QaNav.UserInsight,
    },
    ...this.props.navList,
  ];

  static defaultProps = new Props();

  goToUserSetting = () => {
    const { origin } = this.props;
    const url = `${origin}/userSetting/groupManage`;

    window.location.href = url;
  };

  render() {
    const {
      userInfo,
      origin,
      helpCenterTitle,
      helpCenterList,
      instance,
      showMessageCenter,
      approvalData,
      applicationData,
      onMessageTabChange,
      onAgreeApproval,
      onRefuseApproval,
      onCancelApproval,
    } = this.props;

    return (
      <div className="qa-Nav">
        <div className="left-area">
          <img src="https://img.alicdn.com/tfs/TB1J7Mlf2b2gK0jSZK9XXaEgFXa-714-98.png" className="project-icon" />
          <span className="sub-title">{I18N.QaNav.UserInsightMarketing}</span>
          {instance && (
            <a className="version" href={instance.renewUrl}>
              <span className="version-icon" title={instance.versionName}>
                <Icon type="oned-icon icon-vip-special" />
              </span>
              <span className="version-des">{instance.versionName}</span>
            </a>
          )}
        </div>
        <div className="middle-area">
          {this.NavList.map(nav => (
            <a
              key={nav.key}
              className={window.location.pathname.split('/')[1] === nav.key ? 'active' : ''}
              href={nav.origin ? `${nav.origin}/${nav.key}` : `${origin}/${nav.key}`}>
              {nav.title}
            </a>
          ))}
        </div>
        <div className="right-area">
          {showMessageCenter && (
            <MessageNotification
              approvalData={approvalData}
              applicationData={applicationData}
              onMessageTabChange={onMessageTabChange}
              onAgreeApproval={onAgreeApproval}
              onRefuseApproval={onRefuseApproval}
              onCancelApproval={onCancelApproval}
            />
          )}
          <HelpCenter helpCenterTitle={helpCenterTitle} helpCenterList={helpCenterList} />
          {userInfo.isAdmin && <AntdIcon type="setting" className="user-setting" onClick={this.goToUserSetting} />}
          <LangSwitch />
          <Avatar userInfo={userInfo} instance={instance} />
        </div>
      </div>
    );
  }
}

export default QaNav;

