/**
 * @author zongquan.hzq
 * @description Select 可配置数据源请求方式
 * 考虑多多选和单选很多行为模式不相同，为了避免 props 太多导致用户混淆，因此分拆成两个组件。
 * NOTE: 当 value 是数组时，默认开启多选模式。
 * 当 value 是 string/number 形态时，Select 默认向当前 data 匹配数据项，匹配不到则展示 not found。
 * 当 value 是 对象形态时，Select 展示该选项，并触发 onChange
 */
import { Select, Spin } from 'antd';
import * as _ from 'lodash';
import * as React from 'react';
import { SelectProps } from 'antd/lib/select';
import * as classnames from 'classnames';
import { Override } from '../utils/typing';

const Option = Select.Option;

export class EnhancedSelectItem {
  text: string | React.ReactElement<any>;
  value: string | number;
}

export type EnhancedSelectValue = string | number | EnhancedSelectItem;

export function extractValue(selectValue: EnhancedSelectValue): number | string {
  const selectValueAlias = selectValue as any;
  return selectValueAlias && selectValueAlias.value !== undefined ? selectValueAlias.value : selectValueAlias;
}

export interface EnhancedSelectOwnProps {
  /** 指定 Select Option 数据 */
  data?: EnhancedSelectItem[];
  /** 自定义数据请求方法 */
  fetchData?: (keyword: string) => Promise<EnhancedSelectItem[]>;
  /** 指定当前选中的 Option value */
  value?: EnhancedSelectValue;
  /** 默认选中的 Option value */
  defaultValue?: EnhancedSelectValue;
  /** 是否在组件初始时请求数据，默认是 true */
  shouldFetchOnMount?: boolean;
  /** 触发的 onChange 方法 */
  onChange?: (value: string | number, item: EnhancedSelectItem) => any;
  mode: never;
  optionLabelProp: never;
}

type EnhancedSelectProps = Override<SelectProps, EnhancedSelectOwnProps>;

export class EnhancedSelectState {
  data = [] as EnhancedSelectItem[];
  value = null as string | number;
  input = '';
  fetching = false;
}

/**
 * @author zongquan.hzq
 * @description 支持不同业务场景的 Select 选择器。
 * @classdesc 文档请参看 http://Enhancedesign.alibaba-inc.com/react-components-select-select
 */
export class EnhancedSelect extends React.Component<EnhancedSelectProps, EnhancedSelectState> {
  static defaultProps = {
    onChange: () => {},
    shouldFetchOnMount: true,
  };

  state = new EnhancedSelectState();

  extractValueFromProps(props: EnhancedSelectProps) {
    const value = props.defaultValue === undefined ? props.value : (props.defaultValue as any);

    return extractValue(value);
  }

  constructor(props: EnhancedSelectProps) {
    super(props);

    /** 拿到 value */
    this.state.value = this.extractValueFromProps(this.props);

    /** 拿到 data */
    if (!this.props.fetchData && this.props.data && this.props.data.length) {
      this.state.data = this.props.data;
    }
  }

  lastFetchId = 0;

  async handleSearch(input: string) {
    const { fetchData } = this.props;

    if (!fetchData) {
      return;
    }

    this.lastFetchId += 1;
    const fetchId = this.lastFetchId;

    this.setState({
      input,
      fetching: true,
    });

    const data = await this.props.fetchData(input);

    // 确保拿到的数据是最后发请求的数据。
    if (this.lastFetchId !== fetchId) {
      return [];
    }

    this.setState({
      data,
      fetching: false,
    });
    return data;
  }

  componentDidMount() {
    if (this.props.shouldFetchOnMount) {
      this.handleSearch('');
    }
  }

  /** 兼容 dump 组件，value 和 data 可由父级指定 */
  componentWillReceiveProps(nextProps: EnhancedSelectProps) {
    const currValue = this.extractValueFromProps(this.props);
    const newValue = this.extractValueFromProps(nextProps);

    if (currValue !== newValue) {
      this.setState({
        value: newValue,
      });
    }

    if (nextProps.data !== this.props.data && nextProps.data) {
      this.setState({
        data: nextProps.data,
      });
    }
  }

  hanldeSelect(value: EnhancedSelectValue) {
    const { onChange } = this.props;
    const innerValue = extractValue(value);

    if (innerValue !== this.state.value) {
      const item = _.find(this.state.data, { value: innerValue });
      this.setState({
        value: innerValue,
      });

      onChange(innerValue, item);
    }
  }

  render() {
    const { placeholder, defaultValue, onChange, className, ...rest } = this.props;
    const classes = classnames('Enhanced-select', className);
    const { data } = this.state;
    const { value, fetching } = this.state;
    const propsValue = this.props.value as EnhancedSelectItem;
    let selectValue = value;

    if (propsValue && propsValue.text) {
      selectValue = propsValue.text as any;
    }

    const options = fetching ? (
      <Option key="loading">
        <Spin size="small" />
      </Option>
    ) : (
      data.map(item => {
        return (
          <Option key={String(item.value)} value={item.value}>
            {item.text}
          </Option>
        );
      })
    );

    return (
      <Select
        {...rest}
        className={classes}
        showSearch
        placeholder={placeholder}
        notFoundContent={fetching ? <Spin size="small" /> : null}
        value={selectValue}
        onSelect={this.hanldeSelect.bind(this)}
        onSearch={this.props.fetchData ? this.handleSearch.bind(this) : undefined}
        filterOption={
          this.props.fetchData
            ? false
            : (input, option) => (option.props.children as string).toLowerCase().indexOf(input.toLowerCase()) >= 0
        }
        dropdownMatchSelectWidth={true}>
        {options}
      </Select>
    );
  }
}
