import { Icon } from '../../Icon';
import * as moment from 'moment';
import * as React from 'react';
import { formatTime } from './utils';

interface Props {
  icon: string;
  title: string;
  time: moment.Moment;
  detail: string;
  url: string;
}

export const NotificationItem: React.FunctionComponent<Props> = props => {
  const formattedTime = formatTime(props.time);
  return (
    <a className="notification-item" href={props.url} target="_blank">
      <div className="notification-item-summary">
        <div className="notification-item-icon-wrapper">
          <Icon type={props.icon} className="notification-item-icon" />
        </div>
        <div className="notification-item-title">{props.title}</div>
        <div className="notification-item-time">{formattedTime}</div>
      </div>
      <div className="notification-item-detail">{props.detail}</div>
    </a>
  );
};
