/**
 * @file 全局顶部导航栏
 * @author linfeng
 */

import { Dropdown, Menu, Switch, Tooltip, message } from 'antd';
import { Icon } from '../Icon';
import * as React from 'react';
import * as _ from 'lodash';
import * as moment from 'moment';
import Avatar, { UserInfo, AvatarProps } from './Avatar/Avatar';
import NotificationBox, { NotificationInfo } from './NotificationBox';
import I18N from '../utils/I18N';

const HambergerMenu = [
  {
    title: I18N.Nav.home,
    path: '/home/',
    icon: 'icon-menu-home',
  },
  {
    title: I18N.Nav.dev,
    path: '/dev/',
    icon: 'icon-menu-dev',
  },
  {
    title: I18N.Nav.assets,
    path: '/assets/',
    icon: 'icon-menu-assets',
  },
  {
    title: I18N.Nav.service,
    path: '/oneservice/public/',
    icon: 'icon-menu-service',
  },
  {
    title: I18N.Nav.dataplan,
    path: '/dataplan/',
    icon: 'icon-menu-dataplan',
  },
  {
    title: I18N.Nav.manageCenter,
    path: '/manage/',
    icon: 'icon-management',
  },
];

interface MenuData {
  title: string;
  path: string;
}

type Product = 'service' | 'assets';

interface Props {
  /** 当前模块的 key, 目前只有服务和资产，日后有新的模块可以再添加 */
  product: Product;
  /** 当前模块的菜单数据 */
  menuData: MenuData[];
  /** 通知信息 */
  notificationInfo?: NotificationInfo;
  /** 用户信息 */
  userInfo: UserInfo;
  /** 区分公共云和私有云部署 */
  isPublicCloud?: boolean;
}

class State {
  /** 是否正在执行动画 */
  animating = true;
}

class Nav extends React.Component<Props, any> {
  state = new State();

  static defaultProps = {
    notificationInfo: {
      messageCounts: {},
      taskCounts: {},
      messages: [],
      tasks: [],
      messagesLoading: false,
      tasksLoading: false,
    },
    isPublicCloud: false,
  };

  componentDidMount() {
    try {
      /** TODO：目前暂时用于控制’萃取‘，’服务‘上线之后，需要拓展nav menu的逻辑 */
      const newState: Partial<State> = {};

      this.setState(newState as State);
    } catch (err) {
      message.error(err.message);
    }
  }

  renderHambergerMenu = () => {
    const { product } = this.props;
    return (
      <Dropdown
        placement="bottomLeft"
        overlay={
          <Menu>
            {HambergerMenu.map((d, i) => {
              return (
                <Menu.Item key={i} className={product === d.path.replace(/\//g, '') ? 'active' : ''}>
                  <a href={d.path} className={product === d.path.replace(/\//g, '') ? 'active' : ''}>
                    <Icon type={`oned-icon ${d.icon}`} />
                    {d.title}
                  </a>
                </Menu.Item>
              );
            })}
          </Menu>
        }
        getPopupContainer={el => el.parentElement}>
        <span className="menu-handle">
          <Icon type="oned-icon icon-menu" />
        </span>
      </Dropdown>
    );
  };

  renderHelper() {
    const content = (
      <div className="nav-helper">
        <div className="nav-helper-header">{I18N.Nav.helper.title}</div>
        <div className="nav-helper-content">
          <a className="nav-helper-cell" target="_blank" href="https://help.aliyun.com/product/87584.html">
            <Icon type="oned-icon icon-CombinedShape" />
            {I18N.Nav.helper.doc}
          </a>
          <a
            className="nav-helper-cell"
            target="_blank"
            href="https://selfservice.console.aliyun.com/ticket/createIndex">
            <Icon type="oned-icon icon-CombinedShapexiawu" />
            {I18N.Nav.helper.work}
          </a>
        </div>
      </div>
    );

    return (
      <Tooltip placement="bottomRight" title={content} overlayClassName="helper-tooltip">
        <Icon type="oned-icon icon-help-special" style={{ fontSize: 16 }} />
      </Tooltip>
    );
  }

  render() {
    const { menuData, product, notificationInfo, userInfo, isPublicCloud } = this.props;
    let currentTitle = _.get(HambergerMenu.find(d => d.path === `/${product}/`), 'title');
    if (!currentTitle && product === 'service') {
      currentTitle = I18N.Nav.service;
    }
    return (
      <nav className="nav-root">
        {this.renderHambergerMenu()}
        <div className="left-area">
          <a href="/home/" className="logo-link">
            <Icon type="oned-icon icon-logo" />
          </a>
          <i className="dot" />
          <span className="product-title">{currentTitle}</span>
        </div>
        <div className="middle-area">
          {menuData.map((d, i) => {
            let urlPath = d.path.split('?')[0];
            if (urlPath.endsWith('/')) {
              /** 去掉 /，导航下划线高亮 */
              urlPath = urlPath.slice(0, -1);
            }
            const isActive = location.pathname.startsWith(urlPath);

            return (
              <a
                key={i}
                href={d.path}
                className={`nav-cont nav-cont-enter-init nav-menu-${i} ${isActive ? 'active' : ''}`}
                target="_self">
                {d.title}
              </a>
            );
          })}
        </div>
        <div className="right-area" style={{ width: 'auto' }}>
          <div className="nav-toolkit">
            <NotificationBox {...notificationInfo} />
            {this.renderHelper()}
          </div>
          <Avatar
            userInfo={{
              displayName: userInfo.displayName,
              nickName: userInfo.nickName,
              superAdmin: userInfo.superAdmin,
            }}
            isPublicCloud={isPublicCloud}
          />
        </div>
      </nav>
    );
  }
}

export default Nav;

