/**
 * @file 超过固定长度自动 ...，hover 显示全部
 * @author zongquan.hzq
 * @description 关于自动截断各种方案的讨论 https://css-tricks.com/line-clampin/
 */

import * as React from 'react';
import { Tooltip } from 'antd';
import { TooltipPlacement } from 'antd/lib/tooltip';
import { Highlight } from '../Highlight';
import { getShortName } from '../utils/utils';

export class AbbrProps {
  /** 额外的 className */
  className? = '';
  /** 原始文本 */
  text: number | string = '';
  /** 限制长度，以中文字符计算 */
  length?: number;
  /** 展示几行，不能和 **length** 同时设置 */
  line?: number;
  /** 是否斜体显示，用于 Tab 处于 preview 模式等 */
  italic? = false;
  /** 展现位置，默认为 topLeft */
  placement?: TooltipPlacement = 'topLeft';
  /** 高亮关键词 */
  keyword? = '';
  /** 样式 */
  style?: React.CSSProperties;
  // [key: string]: any;
}

// 全局复用 span，用于检测宽高
let span: HTMLSpanElement;

export class Abbr extends React.PureComponent<AbbrProps, any> {
  static defaultProps = new AbbrProps();

  private el: HTMLElement;
  private timer: number = null;

  state = {
    /** 仅对 line 模式生效 */
    needTooltip: false,
  };

  componentDidMount() {
    if (!span) {
      span = document.createElement('span');
      span.style.position = 'absolute';
      span.style.display = 'inline-block';
      span.style.visibility = 'hidden';
      span.style.left = '-9999px';
      document.body.appendChild(span);
    }
  }

  // 检查文本是否溢出
  isOverflowed() {
    // 构建一个元素，对比和 line clamp 元素的高度是否一致，
    // 若不一致说明产生了 「...」
    span.textContent = String(this.props.text);

    // TODO: 常用的影响尺寸的属性
    const properties: Array<keyof CSSStyleDeclaration> = [
      'lineHeight',
      'wordBreak',
      'whiteSpace',
      'hyphens' as any,
      'fontSize',
      'fontFamily',
      'fontStyle',
      'fontWeight',
      'fontVariant',
    ];
    properties.forEach(property => {
      (span.style as any)[property] = getComputedStyle(this.el)[property];
    });
    // 用 offsetWidth 和 Math.ceil 都会导致宽度小数位丢失
    span.style.width = this.el.getBoundingClientRect().width + 'px';
    return span.offsetHeight !== this.el.offsetHeight;
  }

  handleMouseEnter = () => {
    this.timer = window.setTimeout(() => {
      this.timer = null;
      if (this.isOverflowed()) {
        this.setState({
          needTooltip: true,
        });
      }
    }, 100);
  };

  handleMouseLeave = () => {
    if (this.timer !== null) {
      window.clearTimeout(this.timer);
    }
  };

  getDecoratedChildren(text, italic, keyword, className) {
    const el = keyword ? <Highlight text={text} keyword={keyword} /> : text;
    return italic ? <i className={className}>{el}</i> : <span className={className}>{el}</span>;
  }

  // 行模式的实现原理如下：
  // 初始状态下只渲染文本，并监听鼠标移入事件。每当鼠标移入时检测文本是否溢出，若溢出则显示 Tooltip。
  // 当显示的 Tooltip 消失后，再还原到初始状态。
  renderByLine() {
    const { className, text, line, italic, keyword, placement, style: propsStyle = {}, ...restProps } = this.props;
    const { needTooltip } = this.state;
    const shortNameEl = this.getDecoratedChildren(String(text), italic, keyword, className);

    const style = {
      ...propsStyle,
      WebkitLineClamp: line,
    };

    const finalEl = React.cloneElement(shortNameEl, {
      className: 'multi-line',
      style,
      ref: el => {
        this.el = el;
      },
      // 不渲染 Tooltip 的情况下，鼠标移入时需要检测溢出
      onMouseEnter: needTooltip ? undefined : this.handleMouseEnter,
      onMouseLeave: needTooltip ? undefined : this.handleMouseLeave,
      ...restProps,
    });

    if (needTooltip) {
      return (
        <Tooltip
          title={text}
          placement={placement}
          visible
          onVisibleChange={() => {
            // Tooltip 消失后，不渲染 Tooltip
            this.setState({ needTooltip: false });
          }}>
          {finalEl}
        </Tooltip>
      );
    }

    return finalEl;
  }

  renderByLength() {
    const { className, text, length, line, italic, placement = 'topLeft', keyword, ...restProps } = this.props;
    const textStr = text + '';

    const shortName = getShortName(textStr, length);
    const shortNameEl = React.cloneElement(this.getDecoratedChildren(shortName, italic, keyword, className), restProps);

    const nameEl =
      shortName === textStr ? (
        shortNameEl
      ) : (
        <Tooltip
          title={textStr}
          placement={placement}
          overlayStyle={{
            wordWrap: 'break-word',
            wordBreak: 'break-all',
          }}>
          {shortNameEl}
        </Tooltip>
      );

    return nameEl;
  }

  render() {
    const { text, length, line } = this.props;

    if (!text) {
      return null;
    }

    if (line && length) {
      // tslint:disable-next-line
      console.warn('[Abbr] 请不要同时配置 `line` 和 `length` 属性，将优先使用 `line` 属性');
    }

    // 如果设置了 line，则默认为多行渲染模式
    if (line) {
      return this.renderByLine();
    }

    return this.renderByLength();
  }
}
