/**
 * 白色主题配色
 */
const WhiteTheme: monaco.editor.IStandaloneThemeData = {
  base: 'vs',
  inherit: true,
  colors: {
    'editor.background': '#EBEAEF', // 背景色
    'editor.lineHighlightBackground': '#DFDEE2', // 行高亮色
    'editorCursor.foreground': '#171717', // 光标颜色
    'editor.selectionBackground': '#D2D3D6', // 选中文本框颜色
    'editorIndentGuide.background': '#D3D2D6', // 层级提示颜色
    'editorBracketMatch.background': '#D2D3D6', // 选中括号的背景色
    'editor.findMatchBackground': '#FFA011'
  },
  rules: [
    { token: '', foreground: '171717', background: 'EBEAEF' }, // 默认字体颜色,以及右侧 minimap 背景色
    { token: 'keywords', foreground: '00A2FF' }, // 关键词颜色
    { token: 'number', foreground: 'FE6601' }, // 数值颜色
    { token: 'comment', foreground: '999999' }, // 注释颜色
    { token: 'builtinFunctions', foreground: '25b864' }, // 内置函数
    { token: 'function', foreground: 'FF4B61' }, // 函数颜色
    { token: 'operator', foreground: '06B929' }, // 运算符颜色
    { token: 'string', foreground: 'E0A900' }, // 字符串颜色
    { token: 'customKeywords', foreground: '2bc048' }, // 自定义关键词颜色
  ]
};

/**
 * 黑色主题配色
 */
const DarkTheme: monaco.editor.IStandaloneThemeData = {
  base: 'vs-dark',
  inherit: true,
  colors: {
    'editor.background': '#1F2740', // 背景色
    'editor.lineHighlightBackground': '#343F60', // 行高亮色
    'editorCursor.foreground': '#ffffff', // 光标颜色
    'editorIndentGuide.background': '#485065', // 层级提示颜色
    'editor.selectionBackground': '#000', // 选中文本框颜色
    'editorBracketMatch.background': '#000', // 选中括号的背景色
    'editor.findMatchBackground': '#FFC600'
  },
  rules: [
    { token: '', foreground: 'ffffff', background: '353D53' }, // 默认字体颜色
    { token: 'keywords', foreground: '46AAFF' }, // 关键词颜色
    { token: 'string.sql', foreground: 'FFC600' }, // 字符串颜色
    { token: 'comment', foreground: '999999' }, // 注释颜色
    { token: 'builtinFunctions', foreground: '25b864' }, // 内置函数
    { token: 'function', foreground: 'FF4B61' }, // 函数颜色
    { token: 'operator', foreground: '06B929' }, // 运算符颜色
    { token: 'string', foreground: 'E0A900' }, // 字符串颜色
    { token: 'customKeywords', foreground: '2bc048' }, // 自定义关键词颜色
  ]
};

export { WhiteTheme, DarkTheme };
