/**
 * @file 用户头像组件
 * @author shuangyang.ys
 */

import * as React from 'react';
import * as _ from 'lodash';
import moment from 'moment';
import { Abbr } from '../../Abbr/Abbr';
import LangSwitch from '../LangSwitch/LangSwitch';
import { Dropdown, message, Tooltip, Button } from 'antd';
import { Icon } from '../../Icon';
import I18N from '../../utils/I18N';
import Request from '../../utils/Request';

export enum PurchasedServicesEnum {
  /** 数据萃取 */
  DATAPHIN_ONEID = 'DATAPHIN_ONEID',
  /** 数据服务 */
  DATAPHIN_ONESERVICE = 'DATAPHIN_ONESERVICE',
  /** 数据治理 */
  DATAPHIN_DATAASSETS_GOVERNANCE = 'DATAPHIN_DATAASSETS_GOVERNANCE',
  /** 是否是基础研发版, 存在此code即为基础研发版, 不存在则为智能研发版 */
  DATAPHIN_BASIC_GENERIC_DEVELOPMENT = 'DATAPHIN_BASIC_GENERIC_DEVELOPMENT',
  /** 数据质量 */
  DATAPHIN_QUALITY = 'DATAPHIN_QUALITY',
}

const FeatureCodeMap = {
  [PurchasedServicesEnum.DATAPHIN_DATAASSETS_GOVERNANCE]: I18N.Nav.avatar.dataAssets,
  [PurchasedServicesEnum.DATAPHIN_QUALITY]: I18N.Nav.avatar.assetsQuality,
  [PurchasedServicesEnum.DATAPHIN_ONEID]: I18N.Nav.avatar.distill,
  [PurchasedServicesEnum.DATAPHIN_ONESERVICE]: I18N.Nav.avatar.dataService,
};

export interface UserInfo {
  displayName: string;
  nickName: string;
  superAdmin: boolean;
}

export interface AvatarProps {
  /** 用户名 */
  userInfo: UserInfo;
  /** 是否是公共云版本 */
  isPublicCloud?: boolean;
  /** 头像地址 */
  img?: string;
  /** 额外的 className */
  className?: string;
}

class State {
  regionArea: string = null;

  version: string = null;

  visible = false;

  devTaskAmount: string;

  features: string[] = [];

  versionInfo: any = null;
}

export default class Avatar extends React.Component<AvatarProps, State> {
  state = new State();

  componentDidMount() {
    const { isPublicCloud } = this.props;

    if (isPublicCloud) {
      /** 公共云场景需要提供region信息 */
      Request({ url: '/api/region/current' })
        .then(data => {
          this.setState({
            regionArea: `${_.get(data, 'data.regionArea')}(${_.get(data, 'data.regionCity')})`,
          });
        })
        .catch(err => {
          message.error(err.message);
        });
    } else {
      /** 非公共云场景需要提供dataphin版本信息 */
      Request({ url: '/api/region/prod/version' })
        .then(data => {
          this.setState({
            version: _.get(data, 'data'),
          });
        })
        .catch(err => {
          message.error(err.message);
        });
    }
    /** 获取数据处理单元数 */
    Request({ url: '/api/finance/featureSpecifications' }).then(data => {
      const specItem = (data.data || []).find(item => item.specKey === 'DEV_TASK_AMOUNT');
      this.setState({
        devTaskAmount: specItem.specValue,
      });
    });
    Request({ url: '/api/finance/featureCodes' }).then(data => {
      this.setState({
        features: data.data || [],
      });
    });
    if (isPublicCloud) {
      // 由于这个接口的路径是已有接口的父路径，pont 更新后会导致原有接口的名称变化，暂时绕过
      Request({ url: '/api/finance' }).then(data => {
        this.setState({
          versionInfo: _.get(data, 'data'),
        });
      });
    }
  }

  goToConsole = () => {};

  handleVisibleChange = flag => {
    this.setState({ visible: flag });
  };

  handelCleanNotifications = () => {
    window.localStorage.setItem('notificationStatus', '[]');
  };

  renderContent() {
    const { isPublicCloud } = this.props;
    const { regionArea, version } = this.state;
    const columns = [
      {
        icon: 'icon-language-settings',
        text: I18N.Nav.avatar.languageSetting,
        extra: <LangSwitch />,
      },
      {
        icon: 'icon-locate',
        text: I18N.Nav.avatar.region,
        extra: regionArea,
        disabled: !isPublicCloud,
      },
      {
        icon: 'icon-change-responsible-person',
        text: I18N.Nav.avatar.personalAccount,
        extra: <Icon type="oned-icon icon-jiantouyou" />,
        href: 'https://ram.console.aliyun.com/overview',
        disabled: !isPublicCloud,
      },
      {
        icon: 'icon-huishuarizhi',
        text: I18N.template(I18N.Nav.avatar.version, { val1: version }),
        extra: null,
        disabled: isPublicCloud,
      },
      {
        icon: 'icon-kongzhitaizhuye',
        text: I18N.Nav.avatar.consoleHomePage,
        extra: <Icon type="oned-icon icon-jiantouyou" />,
        href: 'https://dataphin.console.aliyun.com/workingArea',
        disabled: !isPublicCloud,
      },
    ];

    return columns
      .filter(item => !item.disabled)
      .map(item => {
        if (item.href) {
          return (
            <a key={item.icon} className="menu-item menu-item-select" target="_self" href={item.href}>
              <Icon type={`oned-icon ${item.icon}`} />
              <div>{item.text}</div>
              <div className="menu-item-extra">{item.extra}</div>
            </a>
          );
        }
        return (
          <div key={item.icon} className="menu-item">
            <Icon type={`oned-icon ${item.icon}`} />
            <div>{item.text}</div>
            <div className="menu-item-extra">{item.extra}</div>
          </div>
        );
      });
  }

  renderVersionBlock = () => {
    const { userInfo } = this.props;
    const { versionInfo } = this.state;
    if (!versionInfo) {
      return null;
    }

    return (
      <div className="version-block">
        <div className="version-info">
          <div className="expire-time">
            {I18N.template(I18N.Nav.avatar.expirationTime, {
              time: versionInfo.effectiveEndTime,
            })}
          </div>
        </div>

        {userInfo.superAdmin && !versionInfo.whitelistTenant && (
          <div className="button-area">
            <Button
              type="primary"
              className="finance-button"
              onClick={() => {
                window.open(
                  `https://common-buy.aliyun.com/?commodityCode=dataphin_pre&orderType=UPGRADE&instanceId=${
                    versionInfo.instanceId
                  }`
                );
              }}>
              {I18N.Nav.avatar.upgrade}
            </Button>
            <Button
              type="primary"
              className="finance-button"
              onClick={() => {
                window.open(
                  `https://common-buy.aliyun.com/?commodityCode=dataphin_pre&orderType=RENEW&instanceId=${
                    versionInfo.instanceId
                  }#/renew`
                );
              }}>
              {I18N.Nav.avatar.renew}
            </Button>
          </div>
        )}
      </div>
    );
  };

  /** 产品版本和数据处理单元 */
  renderProductHeader() {
    const { features, devTaskAmount } = this.state;
    const isBasicGenericDevelopment = _.get(features, PurchasedServicesEnum.DATAPHIN_BASIC_GENERIC_DEVELOPMENT);
    if (isBasicGenericDevelopment) {
      return `${I18N.Nav.avatar.basicDevelopment} - ${devTaskAmount}${I18N.Nav.avatar.dealUnit}`;
    }
    return `${I18N.Nav.avatar.intelligentDevelopment} - ${devTaskAmount}${I18N.Nav.avatar.dealUnit}`;
  }

  render() {
    const { img, userInfo, className = '', ...restProps } = this.props;
    // 功能包
    const funcPackage = this.state.features
      .map(i => {
        return FeatureCodeMap[i];
      })
      .filter(Boolean)
      .join('; ');
    const productHeader = this.renderProductHeader();
    return (
      <Dropdown
        placement="bottomRight"
        onVisibleChange={this.handleVisibleChange}
        visible={this.state.visible}
        overlay={
          <div className="avatar-dropdown">
            <div className="avatar-card">
              <div className="user-info">
                <img src="https://img.alicdn.com/tfs/TB1JQxotDtYBeNjy1XdXXXXyVXa-818-818.png" />
                <div>
                  <div className="user-info-row-1">
                    <Abbr text={userInfo.displayName} length={12} />
                    <Icon type="oned-icon icon-edit" onClick={this.goToConsole} />
                  </div>
                  <div className="user-info-row-2">
                    <Abbr text={userInfo.nickName} length={12} />
                  </div>
                </div>
              </div>
              <div className="product-info">
                <div className="product-info-header">{productHeader}</div>
                <div className="product-info-features">
                  {I18N.Nav.avatar.features}
                  {funcPackage}
                </div>
              </div>

              {this.renderVersionBlock()}
              <div className="menu">
                {this.renderContent()}
                <a target="_self" onClick={this.handelCleanNotifications} href="/logout.htm" className="menu-item">
                  <Icon type="oned-icon icon-sign-out" />
                  {I18N.Nav.avatar.exitAccount}
                </a>
              </div>
            </div>
          </div>
        }>
        <img className="avatar-placeholder" src="https://img.alicdn.com/tfs/TB1JQxotDtYBeNjy1XdXXXXyVXa-818-818.png" />
      </Dropdown>
    );
  }
}

