/**
 * @description Table 组件封装
 * @author 会影
 */

import { ColumnProps as AntdColumnProps, TableProps } from 'antd/lib/table/interface';
import * as React from 'react';
import { Table as AntdTable } from 'antd';
import { PaginationProps } from 'antd/lib/pagination/Pagination';
import _ from 'lodash';
import classNames from 'classnames';

// 左右间距 20px
const ROW_PADDING = 20;
// 默认单元格宽度
const DEFAULT_CELL_WIDTH = 200;
// 单元格间隔 12px ~ 20px
const MAX_CELL_PADDING = 20;
const MIN_CELL_PADDING = 12;
// 默认宽度
const DEFULT_WIDTH = 200;
// 单元格对应行高
const CELL_HEIGHT = {
  text: { height: 36, content: 12 },
  'text-two': { height: 54, content: 30 },
  component: { height: 44, content: 28 },
  'component-two': { height: 62, content: 46 },
};
type CELL_HEIGHT_TYPE = keyof typeof CELL_HEIGHT;

export interface ColumnProps<T> extends AntdColumnProps<T> {
  type?: CELL_HEIGHT_TYPE;
  canFilter?: boolean;
}

interface MyPaginationProps extends PaginationProps {
  hideOnSinglePage?: boolean;
}

interface Props<T> extends TableProps<T> {
  /** 外部传入 Table 宽度 */
  width?: number;
  /** 是否使用 auto 布局，默认使用 fixed */
  isAuto?: boolean;
  /** 页码 */
  pagination?: MyPaginationProps | false;
  /** 应该使用 EnhancedTable 特有的类型 */
  columns: Array<ColumnProps<T>>;
}

export default class Table<T> extends React.Component<Props<T>, any> {
  isPercent: React.ReactChild;
  static defaultProps = {
    columns: [],
  };
  tableContent: HTMLDivElement;
  static Column = AntdTable.Column;
  static ColumnGroup = AntdTable.ColumnGroup;

  /**
   * 获取单元格高度
   */
  getCellHeight() {
    const children = React.Children.toArray(this.props.children);

    // 有 children 就取 children 的最大值
    if (children.length) {
      return Math.max(...children.map((child: any) => CELL_HEIGHT[child.props.type || 'text'].height), 0);
    }

    // 无 children 去 columns 最大值
    return Math.max(...this.props.columns.map(column => CELL_HEIGHT[column.type || 'text'].height), 0);
  }

  /**
   * 获取每个单元格的边距
   * @param type 单元格类型
   * @param cellHeight 高度
   * @param index 单元格在行中的索引
   */
  getCellSize(type: CELL_HEIGHT_TYPE, cellHeight: number, index: number) {
    if (index === 0) {
      return {
        titlePadding: `10px 0px 10px 20px`,
        paddingLeft: 20,
        paddingRight: 0,
      };
    } else if (index === React.Children.count(this.props.children) - 1) {
      return {
        titlePadding: `10px 20px 10px 0px`,
        paddingLeft: 0,
        paddingRight: 20,
      };
    }

    return {
      titlePadding: `10px 12px 10px 0px`,
      paddingLeft: 0,
      paddingRight: 12,
    };
  }

  render() {
    const {
      pagination = { hideOnSinglePage: true, total: (this.props.dataSource || []).length },
      ...restProps
    } = this.props;

    let finalPagination: MyPaginationProps;
    // 只有一页时，默认隐藏分页
    if (pagination === true) {
      finalPagination = { hideOnSinglePage: true, total: (this.props.dataSource || []).length };
    } else if (typeof pagination === 'object' && pagination.hideOnSinglePage !== false) {
      pagination.hideOnSinglePage = true;
      finalPagination = pagination;
    }

    const cellHeight = this.getCellHeight();

    const childrenCount = React.Children.toArray(restProps.children).length || this.props.columns.length;

    const children = React.Children.map(restProps.children, (child: React.ReactElement<ColumnProps<any>>, index) => {
      const props = child.props;
      const { type = 'text' } = props;

      const cellClassName = classNames(props.className, 'column-content', {
        'column-content-first': index === 0,
        'column-content-last': index === childrenCount - 1,
      });

      const { titlePadding, paddingLeft, paddingRight } = this.getCellSize(type, cellHeight, index);

      return React.cloneElement(child as any, {
        title: (
          <div
            className="column-title"
            style={{
              padding: titlePadding,
            }}>
            {props.title}
          </div>
        ),
        render: (text, record, i) => {
          return (
            <div
              className={cellClassName}
              style={{
                height: `${cellHeight}px`,
                paddingLeft,
                paddingRight,
              }}>
              {props.render ? props.render(text, record, i) : text}
            </div>
          );
        },
      });
    });

    const columns = this.props.columns.map((column, index) => {
      const { type = 'text' } = column;

      const cellClassName = classNames(column.className, 'column-content', {
        'column-content-first': index === 0,
        'column-content-last': index === childrenCount - 1,
      });

      const { titlePadding, paddingLeft, paddingRight } = this.getCellSize(type, cellHeight, index);

      return {
        ...column,
        title: (
          <div
            className="column-title"
            style={{
              padding: titlePadding,
            }}>
            {column.title}
          </div>
        ),
        render: (text, record, i) => {
          return (
            <div
              className={cellClassName}
              style={{
                height: `${cellHeight}px`,
                paddingLeft,
                paddingRight,
              }}>
              {column.render ? column.render(text, record, i) : text}
            </div>
          );
        },
      };
    });

    const realHeight = cellHeight * (this.props.dataSource || []).length;
    const scroll = {
      // 防止不需要 overflow 的 table 出现竖向滚动条
      y: this.props.scroll && this.props.scroll.y < realHeight ? this.props.scroll.y : undefined,
    };

    const className = classNames('oned-table', this.props.className, {
      'onedata-table': true,
      'oned-table-layout-auto': this.props.isAuto,
    });

    return (
      <div className="oned-table-content" style={{ width: this.props.width || '100%' }}>
        <AntdTable<T>
          {...restProps}
          className={className}
          columns={columns.length ? columns : undefined}
          children={React.Children.toArray(children).length ? children : undefined}
          scroll={scroll}
          pagination={finalPagination || pagination}
        />
      </div>
    );
  }
}

