/**
 * @file 空列表组件
 * @description 统一的空内容显示
 * @author 聆一 2018.05.23
 */
import { Button, Icon } from 'antd';
import * as React from 'react';
import I18N from '../../utils/I18N';

class Props {
  /** 为空图片 */

  imgUrl? = 'https://gw.alicdn.com/tfs/TB1wVcys49YBuNjy0FfXXXIsVXa-242-166.png';
  /** 文案提示 */

  text?: React.ReactNode = I18N.Empty.emptyText;
  /** 按钮文案，若为空，不显示按钮 */

  buttonText? = '';
  /** 按钮点击回调 */

  onClick?: (e: React.MouseEvent<HTMLButtonElement>) => void;
}

export default class Empty extends React.Component<Props, any> {
  static defaultProps = new Props();
  render() {
    const { imgUrl, text, buttonText, onClick } = this.props;
    return (
      <div className="dp-empty flex-center-col">
        <img src={imgUrl} />
        <div className="margin-top"> {text} </div>
        {buttonText && (
          <Button className="blue margin-top" onClick={onClick}>
            <Icon type="plus" />
            {buttonText}
          </Button>
        )}
      </div>
    );
  }
}

